.Login{
  position: relative;
  max-width: 300px;
  margin: 0 auto;
  padding: 170px 0;
  box-sizing: border-box;
  .LoginLogo{
    text-align: center;
    margin-bottom: 30px;
    cursor: pointer;
  }
  .LoginNotValue{
    border: 1px solid #f77;
  }
  input{
    width: 100%;
    height: 50px;
    display: block;
    margin: 0;
    padding: 8px 15px 9px;
    border: 1px solid #dbdbdb;
    background-color: #fff;
    color: #000;
    border-radius: 4px;
    box-sizing: border-box;
    font-family: Noto Sans KR,Noto Sans CJK KR,맑은 고딕,Malgun Gothic,sans-serif;
    font-size: 15px;
    line-height: 21px;
    resize: none;
    &:focus{
      outline: none;
    }
  }
  .LoginButton{
    user-select: none;
    display: inline-block;
    margin: 20px 0;
    width: 100%;
    padding: 15px 10px;
    line-height: 20px;
    font-size: 17px;
    min-height: 50px;
    background-color: #35c5f0;
    border-color: #35c5f0;
    color: #fff;
    cursor: pointer;
    border-radius: 4px;
    font-family: Noto Sans KR,Noto Sans CJK KR,맑은 고딕,Malgun Gothic,sans-serif;
    font-weight: 700;
    text-decoration: none;
    box-sizing: border-box;
    border: 1px solid transparent;
  }
  .LoginUrl{
    text-align: center;
    font-size: 15px;
    span{
      display: inline-block;
      margin: 0 8px;
    }
    .LoginSNS{
      margin: 35px 0 15px;
      color: #757575;
      font-size: 12px;
      font-weight: normal;
      line-height: 1.33;
      text-align: center;
    }
    .LoginSNSLogo{
      img{
        cursor: pointer;
        margin: 0 10px;
      }
    }
    .LoginErr{
      cursor: pointer;
      margin-top: 24px;
      color: #c2c8cc;
      font-size: 14px;
      line-height: 18px;
    }
  }
  .LoginNoMember{
    margin-top: 40px;
    border-top: 1px solid #eee;
    .LoginNoMemberOrder{
      cursor: pointer;
      font-size: 15px;
      margin-top: 25px;
      text-align: center;
      line-height: 1.4;
      color: #424242;
      margin-bottom: 20px;
    }
  }
  .Loginfooter{
    position: absolute;
    bottom: -9px;
    left: 55px;
    color: #757575;
    font-size: 10px;
    line-height: 20px;
    text-align: center;
  }
}