.interior3ds {
  margin-top: 20px;
}
.interior3ds .interior3dsNav {
  margin-bottom: 40px;
}
.interior3ds .interior3dsNav span {
  display: inline-block;
  margin-left: 15px;
  width: 360px;
  height: 50px;
  color: rgb(47, 52, 56);
  border: 1px solid rgb(218, 221, 224);
  border-radius: 4px;
  font-size: 16px;
  font-weight: 700;
  line-height: 50px;
  box-sizing: border-box;
  text-align: center;
  cursor: pointer;
}
.interior3ds .interior3dsNav span:first-child {
  border: none;
  background: none rgb(53, 197, 240);
  color: #fff;
}
.interior3ds .interior3dsOption {
  position: relative;
  margin-bottom: 25px;
}
.interior3ds .interior3dsOption span {
  display: inline-block;
  margin-right: 8px;
  font-weight: 700;
  background-color: #f5f5f5;
  border-color: #f5f5f5;
  color: #757575;
  font-size: 15px;
  line-height: 22px;
  padding: 8px 10px;
  border-radius: 4px;
  cursor: pointer;
}
.interior3ds .interior3dsOption span svg {
  vertical-align: middle;
}
.interior3ds .interior3dsOption .interior3dsModal {
  position: absolute;
  z-index: 100;
  box-shadow: 2px 2px 21px -4px rgba(0, 0, 0, 0.73);
  background: #fff;
}
.interior3ds .interior3dsOption .interior3dsModal p {
  border: 1px solid lightgray;
  padding: 15px 30px;
  cursor: pointer;
}
.interior3ds .interior3dsDiv {
  font-size: 14px;
  line-height: 16px;
}
.interior3ds .interior3dsMain {
  display: flex;
  flex-wrap: wrap;
}
.interior3ds .interior3dsMain .interior3ds_form {
  width: 368px;
  margin: 15px 5px;
}
.interior3ds .interior3dsMain .interior3ds_form .userimg_image_frame {
  overflow: hidden;
  height: 260px;
  margin-bottom: 10px;
  border-radius: 5px;
}
.interior3ds .interior3dsMain .interior3ds_form .userimg_image_frame img {
  width: 100%;
  transition: all 0.3s;
  height: 100%;
  border-radius: 5px;
}
.interior3ds .interior3dsMain .interior3ds_form .userimg_image_frame img:hover {
  transform: scale(1.1);
}
.interior3ds .interior3dsMain .interior3ds_form .interior3ds_subtitle {
  text-align: center;
  font-size: 15px;
  line-height: 26px;
  font-weight: 700;
  color: #2f3438;
}
.interior3ds .interior3dsMain .interior3ds_form .userimg_address {
  font-size: 13px;
  line-height: 16px;
  margin-top: 6px;
  color: #2f3438;
  text-align: center;
}
.interior3ds .interior3dsMain .interior3ds_form .userimg_rewie {
  color: rgb(130, 140, 148);
  font-size: 13px;
  line-height: 16px;
  margin-top: 10px;
  text-align: center;
}/*# sourceMappingURL=interior3ds.css.map */